import { Injectable } from '@angular/core';
import {Observable, of} from 'rxjs';
import {defaultQuiz, orderOfDoctrina, orderOfReciprocus, Personality, Quiz} from '../models/quiz';

@Injectable({
  providedIn: 'root'
})
export class QuizService {

  answers: QuizAnswer[] = [];

  constructor() { }

  getQuizzes(): Observable<number[]> {
    return of([1]);
  }


  getQuiz(id: number): Observable<Quiz>  {
    return of(defaultQuiz);
  }

  addAnswer(answer: QuizAnswer): void {
    this.answers.push(answer);
  }

  saveAnswers(): Observable<Personality> {
    return of(orderOfReciprocus);
  }

}

export class QuizAnswer {
  quizId: number;
  questionId: number;
  answer: string | number;
}

