import {Component, EventEmitter, Input, OnChanges, OnInit, Output} from '@angular/core';
import {Question, QuestionType, Quiz} from '../../models/quiz';
import {QuizAnswer} from '../../services/quiz.service';
import {combineLatest, Observable} from 'rxjs';
import {map} from 'rxjs/operators';

@Component({
  selector: 'app-quiz-question',
  templateUrl: './quiz-question.component.html',
  styleUrls: ['./quiz-question.component.scss']
})
export class QuizQuestionComponent implements OnInit {
  @Input() quiz$: Observable<Quiz>;
  @Input() questionNumber$: Observable<number>;
  @Output() answeredEvent = new EventEmitter<QuizAnswer>();
  question$: Observable<Question>;
  questionTypes = QuestionType;
  answer: string | number | null = null;

  constructor() { }

  ngOnInit() {
    this.question$ = combineLatest([this.quiz$, this.questionNumber$]).pipe(
      map(([quiz, questionNumber]) => quiz.questions.find(x => x.order === questionNumber))
    );
  }


  createAnswer(quizId: number, questionId: number) {
    this.answeredEvent.emit({quizId, questionId, answer: this.answer});
    this.answer = null;
  }

}
