import {Component, OnInit} from '@angular/core';
import {BehaviorSubject, combineLatest, Observable} from 'rxjs';
import {Personality, Quiz} from '../models/quiz';
import {QuizAnswer, QuizService} from '../services/quiz.service';
import {shareReplay, take} from 'rxjs/operators';

@Component({
  selector: 'app-quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.scss']
})
export class QuizComponent implements OnInit {

  quiz$: Observable<Quiz> = this.quizService.getQuiz(1).pipe(shareReplay(1));
  question = new BehaviorSubject<number>(1);

  result$: Observable<Personality>;
  finished: boolean = false;


  constructor(private quizService: QuizService) {
  }

  ngOnInit() {
  }

  handleAnswerEvent(event: QuizAnswer) {
    this.quizService.addAnswer(event);
    combineLatest(this.quiz$, this.question)
      .pipe(take(1))
      .subscribe(([quiz, questionNumber]) => {
        const isLastQuestion = quiz.questions.find(c => c.order === (questionNumber + 1));
        if (isLastQuestion === undefined) {
          this.finishQuiz();
        }
        this.question.next(questionNumber + 1);
      });
  }

  finishQuiz(): void {
    this.finished = true;
    this.result$ = this.quizService.saveAnswers();
  }

}
